import './result.css';

import React from 'react';
import { connect } from 'react-redux';

import { renderEditMode } from './utils.js';

class Result extends React.Component {
  constructor(props) {
    super(props);

    this.onEdit = this.onEdit.bind(this);
  }

  onEdit(event) {
    this.props.updateResult({
      [event.target.name]: event.target.value
    });

    event.preventDefault();
  }

  render() {
    return (
      <div className="jumbotron">
        <h4 className={`result ${this.props.mode}`}>
          {renderEditMode(this.props.result, this.props.selectTab, this.onEdit)}
        </h4>
      </div>
    );
  }
};

const mapStateToProps = (state) => ({
  result: state.result.value,
  mode: state.result.mode
});

const mapDispatchToProps = (dispatch) => ({
  updateResult: (result) => dispatch({ type: "UPDATE_RESULT", result }),
  selectTab: (tab) => dispatch({ type: "SELECT_TAB", tab }),
});

Result = connect(
  mapStateToProps,
  mapDispatchToProps
)(Result);

export default Result;



