import React from 'react';
import AutosizeInput from 'react-input-autosize';

const Input = ({ name, hasTab, args: [result, selectTab, handleSubmit] }) => (
  <AutosizeInput 
    type="text" 
    name={name} 
    onFocus={() => hasTab ? selectTab(name) : selectTab(null)} 
    value={result[name]} 
    onChange={handleSubmit} 
    className="result-input" />
);

const renderEditMode = (...args) => ( 
  <form>
    <div className="form-inline">
      <label>Elevii </label>
      <Input name="skill" hasTab={true} args={args}/>
      <Input name="content" hasTab={true} args={args}/>
      <Input name="subject" hasTab={false} args={args}/>
      <label>utilizand </label>
      <Input name="resource" hasTab={true} args={args}/>
      <label>realizand </label>
      <Input name="product" hasTab={true} args={args}/>
      <Input name="studentsCount" hasTab={true} args={args}/>
    </div>
  </form>
);

export {
  renderEditMode
};



